<template>
  <div class="card" @click="TurnSongDetail(item)">
    <el-card :body-style="{ padding: '0px' }" class="cardpart">
      <div class="card-body">
        <img :src="item.picUrl" class="image">
        <h5>{{item.name}}</h5>
        <span>{{item.playCount}}</span>
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
    props:['item'],
    methods: {
        TurnSongDetail(item){
            // console.log(item);
            this.$router.push({ name: 'songDetail', params: { item: item }});
        }
    },
}
</script>
<style lang="less" scoped>
.card {
    float: right;
    position: relative;
    .el-card {
      img {
        width: 100%;
      }
      h5{
        margin: 0;
      }
      span {
        position: absolute;
        top: 0;
        right: 0;
        font-size: 10px;
        color: white;
      }
      &.cardpart {
        box-shadow: none;
        border: none;
      }
    }
  }
</style>
