<template>
  <div class="songlist" @click="playMusic(item,list)">
    <div class="left">
      <h5>{{item.name}}</h5>
      <p>
          <i class="vipsq"></i>
        <span>{{item.song.artists[0].name}}</span>/
        <span v-if="item.song.artists[1]">{{item.song.artists[1].name}}</span>
      </p>
    </div>
    <div class="right">
      <i class="video-play"></i>
    </div>
  </div>
</template>
<style lang="less" scoped>
.songlist {
  overflow: hidden;
  .left {
    float: left;
    width: 80%;
    box-sizing: border-box;
    padding-left: 10px;
    h5 {
      margin: 0;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;
      font-size: 17px;
      overflow: hidden;
      font-weight: 400;
    }
    p {
      margin: 0;
      font-size: 12px;
      color: #888;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;
      .vipsq{
              display: inline-block;
               background: url("../image/index_icon_2x.png") no-repeat;
    width: 12px;
    height: 8px;
    margin-right: 4px;
    background-size: 166px 97px;
      }
    }
  }
  .right {
    width: 20%;
    float: right;
    text-align: center;
    .video-play {
      display: inline-block;
      width: 22px;
      height: 22px;
      background: url("../image/index_icon_2x.png") no-repeat;
      background-position: -24px 0;
      background-size: 166px 97px;
    }
  }
}
</style>

<script>
export default {
  props: ["item","list"],
  methods: {
      playMusic(item,list){
          // console.log(item.id);
          let that=this;
         
          axios
        .get("/song/url", {
          params: {
            id: item.id
          }
        })
        .then(function(response) {
          // console.log(response);
          that.$store.commit('Setcurrentmusicurl',response.data.data[0]);
          that.$store.commit('Setmusicid',item.id);
          that.$store.commit('SetcurrentMusic',item);
           that.$store.commit('SetcurrentMusiclist',list);
           that.$store.commit("SetNotChageMusicList",list);
          // console.log(that.personalizedList);
        })
        .catch(function(error) {
          console.log(error);
        });
          
      }
  },
};
</script>